
import {mapState,mapGetters,mapActions,mapMutations} from 'vuex'
import {INCREMENT,MSG} from '../vuex/mutation-types';

import Vue from 'vue'
import router from '../router'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'


var bus =new Vue()
export default{
name:'Index',
components:{
ViewBox,
Swiper
},
data(){
return{
baseList:[{
url: 'javascript:',
img: 'https://ww1.sinaimg.cn/large/663d3650gy1fq66vvsr72j20p00gogo2.jpg',
title: '送你一朵fua'
}, {
url: 'javascript:',
img: 'https://ww1.sinaimg.cn/large/663d3650gy1fq66vw1k2wj20p00goq7n.jpg',
title: '送你一辆车'
}, {
url: 'javascript:',
img: 'https://static.vux.li/demo/5.jpg',
title: '送你一次旅行',
fallbackImg: 'https://ww1.sinaimg.cn/large/663d3650gy1fq66vw50iwj20ff0aaaci.jpg'
}],

show:true,

currentIndex: 0,
timer: ''
}
},
...mapActions({
beforeCreate:'fetchList'
}),
methods:{
handlelist(name){
var names=JSON.stringify(name)
localStorage.setItem('name',names)
this.$router.push({path:'/footer/list'})
},
handleqb(){
this.$router.push({path:'/footer/classify'})
},



},

computed:{
...mapState(['list']),
...mapState(['items'])


},
mounted(){




var login=localStorage.getItem("login")
var lo=JSON.parse(login)

if(lo){
this.show=false
return
}
}

}



